        margin-top: calc(99.99% * (-1/24 * -1) - (30px - 30px * (-1/24 * -1)) + 30px) !important;
    }

    .section.brand-window .text:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }

    .section.brand-window .text:last-child {
        margin-right: 0;
    }

    .section.brand-window .text:nth-child(24 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}

.section.brand-window .text h1 {
    font-size: 32px;
    margin-bottom: 15px;
    color: $blue_light;
}

@media (min-width: $(tablet)) {

    .section.brand-window .text h1 {
        font-size: 36px;
        text-shadow: 2px 0 0 $black;
        color: $white;
    }
}

@media (min-width: $(desktop)) {

    .section.brand-window .text h1 {
        font-size: 46px;
    }
}

.section.brand-window .text button {
    clear: both;
    display: block;
    margin: 20px 0;
}

.section.brand-window .text .body,
        .section.brand-window .text p {
    font-family: 'Open Sans';
    font-size: 18px;
    color: $gray_dark1;
    line-height: 36px;
    font-weight: 600;
}

@media (min-width: $(tablet)) {

    .section.brand-window .text .body,
        .section.brand-window .text p {
        font-size: 24px;
        color: $white;
        text-shadow: 1px 0 0 $black;
    }
}
